import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiMedicineService {
 

  apiUrl = "https://arjunbala.com/DFaculty/api.php/records/medicines"
  constructor(private _http: HttpClient) { }

  getAllMedicine(){
    return this._http.get(this.apiUrl);
  }

  getMedicineById(id:any){
    return this._http.get(this.apiUrl+"/"+id);
  }
  
  delete(id:number){
    return this._http.delete(this.apiUrl+"/"+id);
  }

  insert(form:any){
    return this._http.post(this.apiUrl,form)
  }

  update(form:any,id:number){
    return this._http.put(this.apiUrl + "/" + id,form)

  }

}
