import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { ApiFacultyService } from '../api-faculty.service';
import { Faculty } from '../faculty';


@Component({
  selector: 'app-detail-faculty',
  templateUrl: './detail-faculty.component.html',
  styleUrls: ['./detail-faculty.component.css']
})
export class DetailFacultyComponent implements OnInit {

  id=0;
  facultyDetail : Faculty = new Faculty();
  constructor(
      private _activatedRoute: ActivatedRoute,
      private _api :ApiFacultyService,
      private _route: Router) { }

  ngOnInit(): void {
    this.id = this. _activatedRoute.snapshot.params.id;
    let ob= this._api.getFacultyById(this.id)
    ob.subscribe(
      (res:any)=>{
        this.facultyDetail = res;
      }
    );
  }

  deleteFaculty(){
    let ob = this._api.delete(this.id);
    ob.subscribe((res:any)=>{
      this._route.navigate(['faculty'])
    });   
  }

  editFaculty(){
    let ob = this._api.update(this.facultyDetail,this.id)
    ob.subscribe(()=>{
      this._route.navigate(['faculty/edit',this.id])
    });
  }
  back(){
    this._route.navigate(['faculty']);
  }

}
