import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { ApiMedicineService } from '../api-medicine.service';

@Component({
  selector: 'app-medicine',
  templateUrl: './medicine.component.html',
  styleUrls: ['./medicine.component.css']
})
export class MedicineComponent implements OnInit {
  
   
  medicine : any[] = [];
  
  constructor(private _activatedRoute:ActivatedRoute,private _api:ApiMedicineService,private _router : Router) { }

  ngOnInit(): void {
    let ob = this._api.getAllMedicine();
    ob.subscribe((res:any)=>{
      this.medicine = res.records;
    });
  }

  detailMedicine(id:number){
    this._router.navigate(['medicine',id]);
  }

}
