import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddFacultyComponent } from './add-faculty/add-faculty.component';
import { AddMedicineComponent } from './add-medicine/add-medicine.component';
import { AuthGuard } from './auth.guard';
import { DetailFacultyComponent } from './detail-faculty/detail-faculty.component';
import { DetailMedicineComponent } from './detail-medicine/detail-medicine.component';

import { FacultyComponent } from './faculty/faculty.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { MedicineComponent } from './medicine/medicine.component';

const routes: Routes = [
  {path:"",component:HomeComponent},
  {path:"home",component:HomeComponent},
  {path:"login",component:LoginComponent}, 
  {path:"faculty",component:FacultyComponent,canActivate:[AuthGuard]},
  {path:"faculty/add",component:AddFacultyComponent,canActivate:[AuthGuard]},
  {path:"faculty/edit/:id",component:AddFacultyComponent},
  {path:"faculty/:id",component:DetailFacultyComponent},
  {path:"medicine",component:MedicineComponent,canActivate:[AuthGuard]},
  {path:"medicine/add",component:AddMedicineComponent,canActivate:[AuthGuard]},
  {path:"medicine/edit/:id",component:AddMedicineComponent},
  {path:"medicine/:id",component:DetailMedicineComponent}
  
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
