import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiMedicineService } from '../api-medicine.service';
import { Medicine } from '../medicine';

@Component({
  selector: 'app-add-medicine',
  templateUrl: './add-medicine.component.html',
  styleUrls: ['./add-medicine.component.css']
})
export class AddMedicineComponent implements OnInit {

  id :any;
  medicineDetail : Medicine = new Medicine();
  constructor(private _api:ApiMedicineService,
              private _route:Router,
              private _activateRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.id = this._activateRoute.snapshot.params.id;
    if(this.id==undefined){
      this.id=0;
    }
    else{
      let ob = this._api.getMedicineById(this.id);
      ob.subscribe((res:any)=>{
        this.medicineDetail=res;
      });
    }
  }

  addEditMedicine(){
    if(this.id==0){
      let ob = this._api.insert(this.medicineDetail);
      ob.subscribe(()=>{
      this._route.navigate(['medicine']);
    });
    }

    else{
      let ob = this._api.update(this.medicineDetail,this.id);
      ob.subscribe(()=>{
        this._route.navigate(['medicine']);
      });
    }
  }

  back(){
    this._route.navigate(['medicine']);
  }


}
