import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { ApiMedicineService } from '../api-medicine.service';
import { Medicine } from '../medicine';

@Component({
  selector: 'app-detail-medicine',
  templateUrl: './detail-medicine.component.html',
  styleUrls: ['./detail-medicine.component.css']
})
export class DetailMedicineComponent implements OnInit {

  id=0;
  medicineDetail : Medicine = new Medicine();
  constructor(
      private _activatedRoute: ActivatedRoute,
      private _api :ApiMedicineService,
      private _route: Router) { }

  ngOnInit(): void {
    this.id = this. _activatedRoute.snapshot.params.id;
    let ob= this._api.getMedicineById(this.id);
    ob.subscribe(
      (res:any)=>{
        this.medicineDetail = res;
      }
    );
  }
  deleteFaculty(){
    let ob = this._api.delete(this.id);
    ob.subscribe((res:any)=>{
      this._route.navigate(['medicine'])
    });   
  }

  editFaculty(){
    let ob = this._api.update(this.medicineDetail,this.id)
    ob.subscribe(()=>{
      this._route.navigate(['medicine/edit',this.id])
    });
  }
  back(){
    this._route.navigate(['medicine']);
  }

}
